body{
	font-family:"Noto Sans", sans-serif;
}

.nav{
	height: 108px;
	display: flex;
	justify-content: space-between;	
	background-color: #AA0601;
	color: #FFF;
	font-size: 20px;
	line-height: 30px;
}
.nav ul{
	display: flex;
	justify-content:center;
	align-items: center;
	padding: 40px 64px;
}
.nav ul li{
	padding: 0 32px;
}
.nav ul li a{color:#fff;}
.logo{
	padding: 30px 88px;
}
h1{
	font-size: 64px;
	line-height: 96px;
}
h2{
	font-size: 48px;
	text-align: center;
	padding: 60px 0;
	font-weight: bold;
	color: #650300;
}
h3{
	font-size: 24px;
	font-weight:bold;
	color: #650300;
}
.banner{
	background: url(https://github.com/hexschool/webLayoutTraining1st/blob/master/week3/banner.png?raw=true) center center no-repeat;
	height: 840px;
	color:#650300;
	text-align: right;
	display: block;
	font-weight: bold;
}
.banner-content{
	display: flex;
	justify-content: flex-end;
	align-items: center;
	height: 100%
}
.banner span{
	font-size: 36px;
	line-height: 54px;
}
.container{
	max-width: 1100px;
	margin: 0 auto;
	padding-bottom: 80px;
}
.main{
	background-color: #AA06010D;
}
.main-list{
	display: flex;
	justify-content:center;
  align-items:end;
}
.main-list-item{
	display: flex;
	align-items: center;
	flex-direction: column;
	width: 208px;
  margin: 0px 38px;
}
.main-list-item img{
	padding-bottom: 30px;
}
.main-list-item:last-child img{
	padding-top: 20px;
	padding-bottom:35px;
}
.main-list-item p{
	padding-top: 4px;
	line-height: 24px;
}
.classic-iconw{
  display: none;
}
.classic-list-item{
  display: flex;
  align-items: center;
  padding: 16px;
}
.classic-img{
  display: block;
  width: 635px;
}
.classictext{
  display: flex;
  margin: 0 10px;
}
.classictext h4{
  display: flex;
  font-size: 48px;
  color: #AA0601;
  font-family: "Roboto", sans-serif;
  font-weight: 900;
  font-style: italic;
  margin: 0 15px;
}
.joint{
  background-image: url("https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/week3/bg.png");
  background-position: center bottom;
  background-size: cover;
  background-repeat: no-repeat;
}
.joint h2{
  color: #FFF;
}
.joint-img{
  display: flex;
  justify-content: center;
  align-items: center;
}
.joint-img li{
  position: relative;
  width: 100%;
  padding: 0 15px;
}
.joint-list{
  position: relative;
  
}
.joint-list h3{
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.5);
  font-size: 48px;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
}
.customer-list{
  display: flex;
  flex-wrap: wrap;
}
.customer-list li{
  width: 25%;
}
.contactus-content{
  max-width: 730px;
  margin: 0 auto;
  width: 100%
}
.coustomer-item{
  display: flex;
  flex-direction: column;
  padding: 0 15px;
}
.coustomer-text{
  padding: 16px;
  border: 1px solid #DEE2E6;
  border-top: none;
}
.coustomer-text .name{
  font-size: 14px;
  font-weight: 600;
}
.contactus{
  background-color:#FBF2F2; 
}
.contactus h2,.contactus p,.contactForm{
  color: #111;
  max-width: 730px;
  margin: 0 auto;
  padding: 12px 0;
}
.contactus p{
  font-size: 20px;
  font-weight: 600;
}
.contactForm,.contactForm .form-list{
  display: flex;
  flex-direction: column;
}
.form-list{
  margin: 8px 0;
}
.form-list label,.contactForm div span{
  font-weight: 650;
  margin-bottom: 4px;
}
.form-list textarea {
  height: 136px;
}
.form-list input,.form-list textarea {
  width: 100%;
  padding: 14px 0 14px 16px;
  border: 1.5px solid #000000;
  background: transparent;
}
.contactFormSub{
  width: 50%;
  background: #AA0601;
  color: #FFFFFF;
  margin: 36px auto;
  padding: 16px 0;
  font-size: 16px;
  line-height: 24px;
}
.contactFormSub span{
  margin: 0 auto;
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.footer{
	background: #AA0601;
	color: #fff;
}
.footer-nav{
	display: flex;
	justify-content:space-between;
	font-size: 16px;
	padding-top: 48px;
	padding-bottom: 32px;	
}
.footer-nav ul{
	display: flex;
}
.footer-nav ul:last-child li{
	margin: 0 8px;
}
.footer-contact ul:first-child{
  display: flex;
  padding-bottom: 32px;
}
.footer-contact ul:first-child li{
  margin-right: 24px;
  font-size: 16px;
}
.footer-contact{
  font-size: 36px;
  padding:48px 0 0px 0;
}
.footer-contact ul li{
	display: flex;
	align-items: center;
	font-size: 2.35vw;
}
.footer .container{
display: flex;
justify-content: space-between;
padding-bottom: 30px;
}
.footer-contact ul li img{
  padding-right: 16px;
  height: 1em;
}
.copyrightText,.copyrightList{
  display: flex;
  justify-content: center;
  align-items: center;
}
.copyright{
  display: flex;
  justify-content: space-between;
  padding-top: 48px;
  padding-bottom: 40px;
  max-width: 1100px;
  margin: 0 auto;
  border-top: 1px solid #FFF;
}
.copyrightText img{
  padding-right: 38px;
}
.copyrightList li{
  padding-left: 62px;
}
.qa-banner .container{
  height: 100%;
  position: relative;
}
.qa-banner{
  height: 304px;
  background-image: url("https://github.com/hexschool/webLayoutTraining1st/blob/master/week3/qa.png?raw=true");  
}
.qa-title{
  color: #000000;
  font-size: 64px;
  font-weight: bold;
  display: flex;
  position: absolute;
  bottom: 0;
}
.qa-content{
  padding-top: 80px;
  padding-bottom: 264px;
}
.qa-content h3{
  color: #000000;
  padding-left: 12px;
}
.qa-content h4{
  font-size: 20px;
  font-weight: 700;
  padding-bottom: 16px;
}
.qa-content p{
  font-size: 16px;
  margin-left: 95px;
}
.qa-item{
  padding: 16px;
}
@media only screen and (max-width: 992px){
  
	.nav{padding: 0 20px;}
  .nav ul {padding: 20px 20px;}
  .copyright,.container{max-width: 920px;}
  .qa-content p{margin: 0;}
  .classictext h4{font-size: 32px;}
  .joint-list h3{font-size: 32px;}
}

@media only screen and (max-width: 768px){
  h1{ font-size: 32px;line-height: 45px;}
  .banner span {font-size: 20px;line-height:0px;}
  .banner { background-image: url(https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/week3/banner-sm.png);}
  .banner{height: 394px;}
	.banner .container{height: 394px;}
	.nav{flex-wrap: wrap;justify-content: center;width:100%;height: auto;padding: 0px;}
	.logo{margin: 0 auto;padding: 5px;width: 100%;}
	.logo img{width:48px;display: block;margin: 0 auto;}
	.nav ul {flex-wrap: wrap;width: 100%;padding: 0px;}
	.nav ul li {display: flex;width: 50%;padding: 0px;justify-content: center;margin: 0px 0px;}
  .nav ul li a{border: 1px solid #fff;width: 100%;display: block;text-align: center;padding: 8px 0;}
  .classic-img {max-width: 390px;}
  .classic-list-item:nth-child(2){flex-direction: row-reverse;justify-content: flex-end;}
  .customer-list li{width: 50%;}
  .main-list{flex-wrap: wrap;}
  .main-list-item{width: 237px;}
  .contactus-content{max-width: 450px;}
  .footer-nav ul li{display:block;}
  .container {padding: 0 0 40px 0;}
  .footer-icon{width: 24px;}
  .copyrightText img {display:none;}
  .copyright,.container{max-width: 690px;margin: 0 auto;}
  .classic-icon{width: 32px;}

  
}

@media only screen and (max-width: 576px){
  .footer-contact ul:first-child {display: none;}
  .footer-contact ul li {font-size: 16px;padding: 8px 0;}
  .footer .container {padding-bottom: 24px;}
  .copyright,.container{max-width: 450px;}
  .footer-nav,.copyright{font-size: 14px;}
  .footer-nav ul:first-child li {margin-right: 20px;}
  .copyright {padding-top: 18px;padding-bottom: 24px;align-items: end;flex-wrap: wrap;flex-direction: column;}
  .copyrightList li {padding-left: 0px;padding: 8px 0;}
  .footer-nav ul:last-child li{padding-left: 12px;}
  .classic-list{max-width: 500px;margin: 0 auto;}
  .classic-img {max-width: 270px;}
  .customer-list li{width: 100%;}
  .joint-img {flex-wrap: wrap;}
  .joint-img li {padding: 5px;}
  .joint-list h3 {font-size: 8.5vw;}
  .classictext h4{font-size: 24px;}
  .copyrightText, .copyrightList {
    display: flex;
    justify-content: center;
    align-items: end;
    flex-direction: column;
}
}
@media only screen and (max-width: 375px){
  h2{font-size: 20px;padding: 20px 0;}
  .contactus h2, .contactus p, .contactForm {max-width: 345px;}
  .copyright, .container {max-width: 345px;}
  .footer-nav ul:first-child li {margin: 0 0 0 12px;}
  .footer-nav ul{justify-content: space-between;}
  .footer-contact ul:first-child {display: none;}
  .classic-list-item {position: relative;}
  .classic-img {max-width: 345px;}
  .classic-icon{display: none;}
  .classic-iconw{display: block;}
  .classictext{position: absolute;align-items: center;justify-content: center;color:#fff;}
  .classictext h4 {color:#fff;font-size: 28px;}
  .classic-list-item,.classic-list-item:nth-child(2) {justify-content: center;}
  .copyrightList li {padding: 4px 0;}
}